//====> WP Modules <====//
import {__} from '@wordpress/i18n';

//====> Phenix Modules <====//
import PreviewImage from './preview.jpg';

//====> Edit Mode <====//
export default function Edit({ attributes, setAttributes }) {
    //===> Render <===//
    return (<>{attributes.preview ?  <img src={PreviewImage} alt="" className='fluid' /> : <div {...blockProps}>

    </div>}</>);
}